.myOrderCardMain{
    
background: linear-gradient(180deg, #FF5F6D 0%, #FFC371 486.85%);
    padding: 1em;
    display: flex;
    flex-direction: column;
    width: 90%;

    margin:1em auto;
    border-radius: 1em;
    color: white;
}
.myOrderCardTopParent{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}


.myOrderCardCenterParent{
    margin: 1em;
    display: flex;
    flex-direction: column;
    /* align-items: center; */
}
.myOrderCardBottomParent{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.myOrderCardBtn{
    background: white;
    color: black;
    width: 15em;
    height: 2em;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5em;
    border-radius: 0.7em;
    cursor: pointer;
    transition: all 0.3s;
    text-decoration: none;
    margin-right: 1em;
}

.myOrderCardBtn:hover{
    transform: scale(1.1);
}

.myOrderCardCenterType{
    color: white;
    font-weight: bold;
}

.myOrderCardTopParent{
    
}
.myOrderCardTopDatas{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.myOrderCardTopIcon{
    width: 2em;
    height: 2em;
    margin-right: 1em;
}
.myOrderCardTopOrderNum{
    font-size: 1.3em;
}
.myOrderCardTopDate{
    font-size: 1.1em;
}
.myOrderCardCenterName{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.myOrderCardCenterNameText{
    font-size: 2em;
    margin-right: 1em;
}
.myOrderCardCenterType{
    background-color: rgb(255, 255, 255);
    border-radius: 10em;
    color:black;
    padding: 0.5em 1em;
    font-weight: bold;
}
.myOrderCardCenterDescrition{
    font-size: 1.3em;
    margin: 0.5em 0em;
}
.myOrderCardCenterSkill{

}
.myOrderCardCenterSkill li{
    font-size: 1.1em;
    font-weight: bold;
    color: rgb(214, 214, 214);
}
.myOrderCardCenterSkillText{
    font-size: 1.2em;
    color:rgb(255, 255, 255);
    font-weight: bold;
}
.myOrderCardBottomParent{

}
.myOrderCardBottomRequest{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.myOrderCardBottomRequestIcon{
    width: 2em;
    height: 2em;
    margin-right: 0.5em;
}
.myOrderCardBottomRequestText{
    font-size: 1.3em;
    color: rgb(255, 255, 255);
}